/* ~~~~~~~~~~~~ Shopping cart ~~~~~~~~~~~~~~ */
main {
  display: flex;
  margin-bottom: 15rem;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 1px solid rgb(0, 0, 0); */
}

.cart-items-container {
  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
  width: 65%;
  border-radius: 1rem;
  background-color: aliceblue;
  padding: 15px;
  margin-right: 10px;
  box-shadow: -5px 10px 30px -10px;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(20, 177, 35); */
}

.cart-item {
  display: flex;
  align-items: center;
  border: 0.5px solid rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  padding: 5px;
  margin-bottom: 10px;
  border-radius: 5px;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(109, 177, 20); */
}

.cart-title {
  text-align: center;
  margin-top: 1rem;
  margin-bottom: 1rem;
