  flex-flow: column;
  align-items: center;
  width: 30%;
  border-radius: 1rem;
  background-color: rgba(173, 216, 230, 0.393);
  padding: 10px;
  box-shadow: -5px 10px 30px -10px;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(20, 177, 35); */
}

.summary-title {
  margin-top: 1rem;
  margin-bottom: 1rem;
