  border-radius: 5px;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(164, 20, 177); */
}

.price-summary-container {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
