}

.total-price {
  margin-left: auto;
}

.total {
  margin-top: 15px;
}

button {
  border-radius: 5px;
  /* position: absolute; */
  /* top: 2rem; */
  border: none;
  width: 8rem;
  height: 2rem;
  background-color: rgb(255, 213, 151);
